.infinite-scroll-wrapper {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.bottom-loading {
  height: 1.333333rem;
  line-height: 1.333333rem;
  text-align: center;
  color: #999;
}

.bottom-loading cite {
  position: relative;
  padding-left: 1rem;
  display: none;
}

.bottom-loading cite svg {
  position: absolute;
  top: 50%;
  left: 0;
  width: 0.8rem;
  height: 0.8rem;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.bottom-loading cite.no-data {
  padding-left: 0.666667rem;
}

.bottom-loading cite.no-data:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 0.533333rem;
  height: 0.533333rem;
  background-image: url("../assets/img/components/infinite-no-data@2x.png");
  background-image: -webkit-image-set(url("../assets/img/components/infinite-no-data@3x.png") 3x);
  background-image: image-set(url("../assets/img/components/infinite-no-data@3x.png") 3x);
  background-repeat: no-repeat;
  background-size: 100% auto;
  -webkit-animation: none;
          animation: none;
  -webkit-transform: translateY(-55%);
  transform: translateY(-55%);
}

.bottom-loading.show cite {
  display: inline-block;
}

.bottom-placeholder {
  position: relative;
  overflow: hidden;
}

@-webkit-keyframes infinite-loading {
  0% {
    -webkit-transform: translateY(-50%) rotate(0deg);
    transform: translateY(-50%) rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(-50%) rotate(360deg);
    transform: translateY(-50%) rotate(360deg);
  }
}

@keyframes infinite-loading {
  0% {
    -webkit-transform: translateY(-50%) rotate(0deg);
    transform: translateY(-50%) rotate(0deg);
  }

  100% {
    -webkit-transform: translateY(-50%) rotate(360deg);
    transform: translateY(-50%) rotate(360deg);
  }
}


